@charset "utf-8";
/* CSS Document */
body{font-family:Gotham, "Helvetica Neue", Helvetica, Arial, sans-serif; font-size:14px; color:#000; margin:0; padding:0;}
::selection{background:#3F9;}
*{transition:1s all;} 
 
/*Grid style*/ 
.col-xs-1, .col-xs-2, .col-xs-3, .col-xs-4, .col-xs-5, .col-xs-6, .col-xs-7, .col-xs-8, .col-xs-9, .col-xs-10, .col-xs-11, .col-xs-12 {float:left;}
.col-xs-12 {width:100%;}.col-xs-11 {width:91.66666667%;}.col-xs-10 {width:83.33333333%;}.col-xs-9 {width:75%;}.col-xs-8 {width:66.66666667%;}.col-xs-7 {width:58.33333333%;}.col-xs-6 {width:50%;}.col-xs-5 {width:41.66666667%;}.col-xs-4 {width:33.33333333%;}.col-xs-3 {width:25%;}.col-xs-2 {width:16.66666667%;}.col-xs-1 {width:8.33333333%;}
@media (min-width:768px) {
.col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12 {float:left;}
.col-sm-12 {width:100%;}.col-sm-11 {width:91.66666667%;}.col-sm-10 {width:83.33333333%;}.col-sm-9 {width:75%;}.col-sm-8 {width:66.66666667%;}.col-sm-7 {width:58.33333333%;}.col-sm-6 {width:50%;}.col-sm-5 {width:41.66666667%;}.col-sm-4 {width:33.33333333%;}.col-sm-3 {width:25%;}.col-sm-2 {width:16.66666667%;}.col-sm-1 {width:8.33333333%;}}
@media (min-width:992px) {
.col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-10, .col-md-11, .col-md-12 {float:left;}
.col-md-12 {width:100%;}.col-md-11 {width:91.66666667%;}.col-md-10 {width:83.33333333%;}.col-md-9 {width:75%;}.col-md-8 {width:66.66666667%;}.col-md-7 {width:58.33333333%;}.col-md-6 {width:50%;}.col-md-5 {width:41.66666667%;}.col-md-4 {width:33.33333333%;}.col-md-3 {width:25%;}.col-md-2 {width:16.66666667%;}.col-md-1 {width:8.33333333%;}}
@media (min-width:1200px) {
.col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12 {float:left;}
.col-lg-12 {width:100%;}.col-lg-11 {width:91.66666667%;}.col-lg-10 {width:83.33333333%;}.col-lg-9 {width:75%;}.col-lg-8 {width:66.66666667%;}.col-lg-7 {width:58.33333333%;}.col-lg-6 {width:50%;}.col-lg-5 {width:41.66666667%;}.col-lg-4 {width:33.33333333%;}.col-lg-3 {width:25%;}.col-lg-2 {width:16.66666667%;}.col-lg-1 {width:8.33333333%;}}
/*End*/

.Wrap{float:left; width:100%;} 
.header{float:left; width:100%; background:green; padding:2% 0 0 0; margin:0; line-height:0;}
.container{display:inline-block; padding:0 2%; width:100%; box-sizing:border-box;}
.FL{float:left;}
.FR{float:right;}
.Logo{font-size:24px; font-style:italic; line-height:24px;}
.Search_Right{float:right;}
.Search_Right input[type='text']{float:left; background:#fff; border:1px solid #000; padding:5px 10px; outline:none; font-size:16px; border-right:0; border-radius:4px 0 0 4px;}
.Search_Right button{float:left; border:0; background:#000; color:#fff; font-size:16px; padding:5px 8px; margin:0 0 0 -4px; border:1px solid #000; border-radius:0 4px 4px 0; cursor:pointer;}
/*Main Menu*/
.Main_Menu{float:left; padding:0 2%; background:#06C; width:100%; margin:30px 0 0 0; box-sizing:border-box;}
.Main_Menu ul{float:left; margin:0; padding:0; width:100%;}
.Main_Menu ul li{float:left; list-style:none; width:16.66%; position:relative;}
.Main_Menu ul li a{color:#fff; font-size:18px; text-decoration:none; line-height:50px; display:inline-block; width:100%; text-align:center; border-right:1px solid #fff;}
.Main_Menu ul li .Sub_menu{display:none; position:absolute; background:#042CA7;}
.Main_Menu ul li.Dropdown a{position:relative;}
.Main_Menu ul li.Dropdown a:after{position:absolute; border-color:#fff transparent transparent transparent; content:''; border-style:solid; border-width:8px; top:20px; right:10px;}
.Main_Menu ul li .Sub_menu li{width:100%;}
.Main_Menu ul li .Sub_menu li a{border:0}
.Main_Menu ul li .Sub_menu li a:after{content:normal;}
.Main_Menu ul li:hover{background:#042CA7}
.Main_Menu ul li:hover .Sub_menu{display:block;}
.Main_Menu .Sub_menu li:hover{background:#06C;}
/*End*/

.Main_banner{float:left; width:100%; background:url(../images/header.jpg); background-position:center; padding:5% 0; text-align:center;}
.Main_banner h1{color:#fff; font-size:60px; line-height:1.6em; padding:0 4%; font-family:Baskerville, "Palatino Linotype", Palatino, "Century Schoolbook L", "Times New Roman", serif;}
.Main_banner p{float:left; width:100%; text-align:center;}
.Button{display:inline-block; text-align:center; white-space:nowrap; padding:25px 60px; background:#fff; border-radius:60px; font-size:24px; border:1px solid #000; text-decoration:none;}
.Button:hover{background:#000; border-color:#fff; color:#fff;}
.btn-blue{background:#fff; color:#06C; border-color:#042CA7;}
.btn-blue:hover{background:#06C; color:#fff; border-color:#042CA7;}

.middle{float:left; width:100%;}
.services, .ourteam{float:left; width:100%; box-sizing:border-box; background:#232325; text-align:center; padding-bottom:20px;}
.ourteam{background:#6cc372}
.services h2, .ourteam h2{color:#fff; font-size:50px; font-weight:normal; padding:40px 0 0; margin:0;}
.services p, .ourteam p{color:#c74341; font-size:20px;}
.ourteam p{color:#000;}

.service-row, .team-row{width:80%; margin:50px auto 0px auto}
.service-step{padding:0 10%;}
.service-step img{border:4px solid #232325; border-radius:100%; transition:1.2s ease-in-out;}
.service-step img:hover{transform:rotate(360deg)}
.service-step h2{font-size:28px; padding:20px 0 0;}
.service-step p{font-size:16px;}

.team-step{margin:0 2%; box-sizing:border-box; background:#fff; border-radius:6px; padding:10% 2%;}
.team-step img{display:inline-block; border:5px solid #6cc372; border-radius:100%;} 
.team-step img:hover{transform:scale(0.9); -webkit-transform:scale(1.3); -ms-transform:scale(0.9); -ms-transform:scale(0.9); -o-transform:scale(0.9);}
.team-step h2.team-name{font-size:24px; color:#6cc372}
.team-step button{background:#6cc372 url(../images/twiter-icon.png)10% center no-repeat; width:90%; padding:4% 3%; box-sizing:border-box; border:0; cursor:pointer; color:#fff; font-size:22px; border-radius:6px;}
.team-step button:hover{background-color:#c74341}



.carousel-inner>.item>a>img,.carousel-inner>.item>img,.img-responsive,.thumbnail a>img,.thumbnail>img{display:block;max-width:100%;height:auto}
.carousel{position:relative}.carousel-inner{position:relative;width:100%;overflow:hidden}.carousel-inner>.item{position:relative;display:none;-webkit-transition:.6s ease-in-out left;-o-transition:.6s ease-in-out left;transition:.6s ease-in-out left}.carousel-inner>.item>a>img,.carousel-inner>.item>img{line-height:1}@media all and (transform-3d),(-webkit-transform-3d){.carousel-inner>.item{-webkit-transition:-webkit-transform .6s ease-in-out;-o-transition:-o-transform .6s ease-in-out;transition:transform .6s ease-in-out;-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-perspective:1000px;perspective:1000px}.carousel-inner>.item.active.right,.carousel-inner>.item.next{left:0;-webkit-transform:translate3d(100%,0,0);transform:translate3d(100%,0,0)}.carousel-inner>.item.active.left,.carousel-inner>.item.prev{left:0;-webkit-transform:translate3d(-100%,0,0);transform:translate3d(-100%,0,0)}.carousel-inner>.item.active,.carousel-inner>.item.next.left,.carousel-inner>.item.prev.right{left:0;-webkit-transform:translate3d(0,0,0);transform:translate3d(0,0,0)}}.carousel-inner>.active,.carousel-inner>.next,.carousel-inner>.prev{display:block}.carousel-inner>.active{left:0}.carousel-inner>.next,.carousel-inner>.prev{position:absolute;top:0;width:100%}.carousel-inner>.next{left:100%}.carousel-inner>.prev{left:-100%}.carousel-inner>.next.left,.carousel-inner>.prev.right{left:0}.carousel-inner>.active.left{left:-100%}.carousel-inner>.active.right{left:100%}.carousel-control{position:absolute;top:40%;left:15px;width:40px;height:40px;margin-top:-20px;font-size:1.5em;font-weight:100;line-height:35px;color:#ffffff;text-align:center;background:#B69971;border:3px solid #ffffff;-webkit-border-radius:23px;-moz-border-radius:23px;border-radius:23px;opacity:0.5;filter:alpha(opacity=50);}
.carousel-control.left{background-image:-webkit-linear-gradient(left,rgba(0,0,0,.5) 0,rgba(0,0,0,.0001) 100%);background-image:-o-linear-gradient(left,rgba(0,0,0,.5) 0,rgba(0,0,0,.0001) 100%);background-image:-webkit-gradient(linear,left top,right top,from(rgba(0,0,0,.5)),to(rgba(0,0,0,.0001)));background-image:linear-gradient(to right,rgba(0,0,0,.5) 0,rgba(0,0,0,.0001) 100%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#80000000', endColorstr='#00000000', GradientType=1);background-repeat:repeat-x}.carousel-control.right{right:0;left:auto;background-image:-webkit-linear-gradient(left,rgba(0,0,0,.0001) 0,rgba(0,0,0,.5) 100%);background-image:-o-linear-gradient(left,rgba(0,0,0,.0001) 0,rgba(0,0,0,.5) 100%);background-image:-webkit-gradient(linear,left top,right top,from(rgba(0,0,0,.0001)),to(rgba(0,0,0,.5)));background-image:linear-gradient(to right,rgba(0,0,0,.0001) 0,rgba(0,0,0,.5) 100%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#80000000', GradientType=1);background-repeat:repeat-x}.carousel-control:focus,.carousel-control:hover{color:#fff;text-decoration:none;filter:alpha(opacity=90);outline:0;opacity:.9}
.carousel-control .glyphicon-chevron-left,.carousel-control .glyphicon-chevron-right,.carousel-control .icon-next,.carousel-control .icon-prev{position:absolute;top:50%;z-index:5;display:inline-block;margin-top:-10px}.carousel-control .glyphicon-chevron-left,.carousel-control .icon-prev{left:50%;margin-left:-10px}.carousel-control .glyphicon-chevron-right,.carousel-control .icon-next{right:50%;margin-right:-10px}.carousel-control .icon-next,.carousel-control .icon-prev{width:20px;height:20px;font-family:serif;line-height:1}.carousel-control .icon-prev:before{content:'\2039'}.carousel-control .icon-next:before{content:'\203a'}.carousel-indicators{position:absolute;bottom:10px;left:50%;z-index:15;width:60%;padding-left:0;margin-left:-30%;text-align:center;list-style:none}.carousel-indicators li{display:inline-block;width:10px;height:10px;margin:1px;text-indent:-999px;cursor:pointer;background-color:#000\9;background-color:rgba(0,0,0,0);border:1px solid #fff;border-radius:10px}.carousel-indicators .active{width:12px;height:12px;margin:0;background-color:#fff}.carousel-caption{position:absolute;right:15%;bottom:20px;left:15%;z-index:10;padding-top:20px;padding-bottom:20px;color:#fff;text-align:center;text-shadow:0 1px 2px rgba(0,0,0,.6)}.carousel-caption .btn{text-shadow:none}@media screen and (min-width:768px){.carousel-control .glyphicon-chevron-left,.carousel-control .glyphicon-chevron-right,.carousel-control .icon-next,.carousel-control .icon-prev{width:30px;height:30px;margin-top:-12px;font-size:1.2em}.carousel-control .glyphicon-chevron-left,.carousel-control .icon-prev{margin-left:-15px}.carousel-control .glyphicon-chevron-right,.carousel-control .icon-next{margin-right:-15px}.carousel-caption{right:20%;left:20%;padding-bottom:30px}.carousel-indicators{bottom:20px}}